*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ol,
ul {
  list-style: none;
}

body {
  font-family: $fontFamily;
  font-size: $fontSize + px;
  font-weight: $fontWeight;
  color: $second-font-color;
}

input{
  &:focus {
    outline: none;
  }
}
